import { useState, useEffect } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

// STYLES
import './global/css/normalize.css';

// PAGES
import Accounts from './pages/Accounts';
import Edit from './pages/Edit';
import View from './pages/View';

function App() {
    let [_accounts, _setAccounts] = useState(null);
    
    useEffect(function() {
        if(window.localStorage.getItem('accounts')) {
            _setAccounts(JSON.parse(window.localStorage.getItem('accounts')));
        }
        else {
            let defaultAccounts = [
                {
                    id: 0,
                    image: 1,
                    name: 'Álvaro',
                    isAdmin: true
                },
                {
                    id: 1,
                    image: 2,
                    name: 'Claudia'
                },
                {
                    id: 2,
                    image: 3,
                    name: 'Boss Baby'
                }
            ];
            _setAccounts(defaultAccounts);
            window.localStorage.setItem('accounts', JSON.stringify(defaultAccounts));
        }
    }, []);
    
    return(
        <BrowserRouter basename={ process.env.PUBLIC_URL }>
            <Switch>
                <Route exact path='/courflix/'>
                    {
                        _accounts ? (
                            <Accounts admin={ false } accounts={ _accounts } />
                        ) : null
                    }
                </Route>
                <Route exact path='/courflix/ManageProfiles'>
                    {
                        _accounts ? (
                            <Accounts admin={ true } accounts={ _accounts } />
                        ) : null
                    }
                </Route>
                <Route exact path='/courflix/edit/new'>
                    <Edit account={ false } />
                </Route>
                <Route exact path='/courflix/edit/:id'>
                    {
                        _accounts ? (
                            (props) => (<Edit account={ _accounts[props.match.params.id] } />)
                        ) : null
                    }
                </Route>
                <Route exact path='/courflix/view/:id'>
                    {
                        _accounts ? (
                            (props) => (<View account={ _accounts[props.match.params.id] } />)
                        ) : null
                    }
                </Route>
            </Switch>
        </BrowserRouter>
    );
}

export default App;